import React, {  useEffect, useState } from 'react'
import { useParams } from "react-router-dom";
import LoadingPage from './LoadingPage';

import parse from 'html-react-parser';
import { ref, get } from 'firebase/database'
import { db } from '../firebase/firebase'

const BookPage = () => {

    const [books, setBooks] = useState([])
    const [book, setBook] = useState(null)
    const { id } = useParams();

    const htmlDecode = (htmlString) => {
        if(!!htmlString && htmlString.includes("<div")){
            return parse(htmlString)
        }
        return htmlString 
    }
    
    useEffect(() => {
        const fetchBooks = async () => {
            const bookData = JSON.parse(localStorage.getItem('books'));
            if (!bookData) {
                const snapshot = await get(ref(db, `books/`));
                const bk = [];
                snapshot.forEach((childSnapshot) => {
                    bk.push({
                        id: childSnapshot.key,
                        ...childSnapshot.val()
                    });
                });
                localStorage.setItem('books', JSON.stringify(bk));
                setBooks(bk);
            } else {
                setBooks(bookData);
            }
        };

        fetchBooks();
    }, []);

    useEffect(() => {
        if (books.length > 0) {
            const foundBook = books.find((b) => b.id === id);
            setBook(foundBook);
        }
    }, [books, id]);
    // useEffect(() => {
    //     window.scrollTo(0, 0)
    // }, [id])

    
    return (
        <div>
        {book ? (
            <div className="bookPage">
        <h1>{book.title}</h1>
        <div className="bookPage__container">
            <img alt="" src={"."+book.img} />
            <div className="bookPage__description">
                {htmlDecode(book.description)}
                <span className="bookPage__buyBox">
                    <a href={book.amazonLink}>Buy now</a>
                </span>
            </div>

            
        </div>
        <div className="book-deatails">
            <ul>
            <li>
                Language 
                <img src={"./images/mini_icons/globeIcon.jpg"} alt="" />
                {book.language}
            </li>
            <li>
                Pages 
                <img src={"./images/mini_icons/bookIcon.jpg"} alt=""/>
                {book.pages}
            </li>
            <li>
                Publication Date 
                <img src={"./images/mini_icons/calendarIcon.jpg"} alt=""/>
                {book.publicationDate}
            </li>
            </ul>
        </div> 
     </div>
    ) : (
        <LoadingPage></LoadingPage>
        )}
    </div>)
        
}
    

export { BookPage as default };