import React from 'react'
import { fbLink, igLink, ytLink } from '../data/social_links'

const SocialMedia = (props) => {
    return(
    <div>
    <div className="social-media"> 
        <h3>Find us</h3>
        <ul>

            <li>
                <a href={igLink}>
                    <img alt="" className="bookPage__mini_icon" src={"./images/mini_icons/igIcon.jpg"} />
                </a>
            </li>
            <li>
                <a href={ytLink}>
                    <img alt="" className="bookPage__mini_icon" src={"./images/mini_icons/ytIcon.jpg"} />
                </a>
            </li>
            <li>
                <a href={fbLink}>
                    <img alt="" className="bookPage__mini_icon" src={"./images/mini_icons/fbIcon.jpg"} />
                </a>
            </li>
        </ul>
    </div>
    </div>
    )
}
    

export { SocialMedia as default }