import React from 'react'
import SocialMedia from './SocialMedia'
import { NavLink} from 'react-router-dom';
const Footer = (props) => {
    return (
    <div>
        <div className="footer">
            <section className="footer-pages">
                <h3>Navigate</h3>
                <ul>
                    <li><NavLink to="/bookshelf/home" activeclassname="is-active" >Book Store</NavLink></li>
                    <li><NavLink to="/bookshelf/about" activeclassname="is-active">About</NavLink></li>
                    <li><NavLink to="/bookshelf/contact" activeclassname="is-active">Contact </NavLink></li>
                </ul>
            </section>
            <SocialMedia />
        </div>
    </div>
    )
}

export { Footer as default };