import React, { useEffect, useState } from 'react'
import LoadingPage from './LoadingPage'
import { ref, get } from 'firebase/database'
import { db } from '../firebase/firebase'

// import { fbLink, igLink, ytLink } from '../data/social_links'


const Contact = () => {
    const [contact, setContact] = useState({ig: {}, fb:{}, yt:{}})
    useEffect(() => {
        window.scrollTo(0, 0)
        const fetchContact = async () => {
            let contactData = JSON.parse(localStorage.getItem('contact'))
            if(!contactData) {
                get(ref(db, `contact/`))
                    .then((snapshot) => {
                        contactData = { 
                            fb:{
                                imgLink: snapshot.val().fb.imgLink,
                                siteLink: snapshot.val().fb.siteLink
                            }, 
                            yt:{
                                imgLink: snapshot.val().yt.imgLink,
                                siteLink: snapshot.val().yt.siteLink
                            },
                            ig:{
                                imgLink: snapshot.val().ig.imgLink,
                                siteLink: snapshot.val().ig.siteLink
                            }
                        } 
                        setContact(contactData)
                        localStorage.setItem('contact',JSON.stringify(contactData))
                        console.log("Data written locally")
                    })
            } else 
            {
                const { fb, ig, yt } = contactData
                console.log("Data existed locally")
                setContact({ 
                    ig:{
                        imgLink: ig.imgLink,
                        siteLink: ig.siteLink
                    }, 
                    fb:{
                        imgLink: fb.imgLink,
                        siteLink: fb.siteLink
                    },
                    yt:{
                        imgLink: yt.imgLink,
                        siteLink: yt.siteLink
                    }
                
                })
            } 
        }
        fetchContact()
    },[])

    const showSocial = () => {
        let { fb,ig, yt } = contact
        for (const property in contact) {
            console.log(`${property}: ${contact[property]}`);
        }
    }

    return (
        
        <div>
        {
            contact ? 
            <div className="contact-page"> 
            <ul>
                <li>
                    <h2>Email</h2>
                    <p>bookshelfpremium@gmail.com</p>
                </li>
                {
                    showSocial()
    
                }
                <li>
                    <h2>Instagram</h2>
                    <a href={contact.ig.siteLink}>
                        <img className="bookPage__mini_icon" src={contact.ig.imgLink} alt="" />
                    </a>
                </li>
                <li>
                    <h2>YouTube&nbsp;&nbsp;</h2>
                    <a href={contact.yt.siteLink}>
                        <img className="bookPage__mini_icon" src={contact.yt.imgLink} alt="" />
                    </a>
                </li>
                <li>
                    <h2>Facebook</h2>
                    <a href={contact.fb.siteLink}>
                        <img className="bookPage__mini_icon" src={contact.fb.imgLink} alt="" />
                    </a>
                </li>
            </ul>
        </div>
        :
        <LoadingPage></LoadingPage>
        }
        </div>
    )
}

export { Contact as default }