import React, { useEffect, useState } from "react";
import LoadingPage from './LoadingPage'
import { ref, get } from 'firebase/database'
import { db } from '../firebase/firebase'

const About = () =>  {

    const [about, setAbout] = useState({aboutMe: '', aboutPage: ''})

    useEffect(() => {
        window.scrollTo(0, 0)
        const fetchAbout = async () => {
            let aboutData = JSON.parse(localStorage.getItem('about'))
            if(!aboutData) {
                get(ref(db, `about/`))
                    .then((snapshot) => {
                        aboutData = { aboutMe:snapshot.val().aboutMe , aboutPage: snapshot.val().aboutPage} 
                        setAbout(aboutData)
                        localStorage.setItem('about',JSON.stringify(aboutData))
                        console.log("Data written locally")
                    })
            } else 
            {
                console.log("Data existed locally")
                setAbout({ aboutMe: aboutData.aboutMe, aboutPage: aboutData.aboutPage })
            } 
        }
        fetchAbout()
    },[])

    return (
    <div className="about-me">
    {
        about ? 
        <div className="about-me-text"> 
            <h1>About me</h1>
            <p> {about.aboutMe}</p>
            <h1>About this page</h1>
            <p>{about.aboutPage}</p>
        </div>
        :
        <LoadingPage></LoadingPage>
    }
    </div>
    )
} 

export { About as default }