import { BrowserRouter, Route, Routes, Navigate } from 'react-router-dom';
import React from 'react';
import About from '../components/About';
import BookshelfApp from '../components/BookshelfApp';
import BookPage from '../components/BookPage';
import Contact from '../components/Contact'
import Header from '../components/Header'
import Footer from '../components/Footer'


const AppRouter = () => (
    <BrowserRouter>
        <div>
        <Header />
            <Routes> 
                <Route path="/bookshelf" element={<Navigate to="/bookshelf/home" replace />} />
                <Route path="/bookshelf/" element={<Navigate to="/bookshelf/home" replace />} />
                <Route path="/bookshelf/home"  element={<BookshelfApp />}  />
                <Route path="/bookshelf/book/:id" element={<BookPage />} />
                <Route path="/bookshelf/about" element={<About />} />
                <Route path="/bookshelf/contact" element={<Contact />} />
            </Routes>
        <Footer />
        </div>
    </BrowserRouter>
);

export default AppRouter;