import React from 'react';
import { NavLink} from 'react-router-dom';

const Header = () => {

  return(
    <div className="header">
    <img src="./images/bg_stars.gif" alt="Background" className="header__bg" />
    <div className="header__container">
      <h1>Premium <br></br>Bookshelf</h1>
      <ul>
        <li><NavLink to="/bookshelf/home" activeclassname="is-active" >Book Store</NavLink></li>
        <li><NavLink to="/bookshelf/about" activeclassname="is-active" >About</NavLink></li>
        <li><NavLink to="/bookshelf/contact" activeclassname="is-active">Contact </NavLink></li>
        {/* <li><NavLink to="/test" activeClassName="is-active">Test </NavLink></li> */}
      </ul>
      
    </div>
  </div>
  )
}
export { Header as default }
