import React, { useEffect } from 'react';
import { Link } from 'react-router-dom'


const BookItem = (props) => {
    useEffect(() => {
        window.scrollTo(0, 0)
    },[])
    return (
    <div className="book-banner">
        <Link  to={"/bookshelf/book/" + props.id} >
            <img alt={props.title}  src={props.img}/>
            <p >{props.title.split(':')[0]}</p>
            { props.title.split(':')[0].length <= 24 && <br></br>}
        </Link>
    </div>
    );
}

export { BookItem as default}
