import React, { useEffect, useContext } from 'react';
import BookContext from '../context/BookContext.js'
import BookItem from './BookItem';

const BookList = () =>  {

    const books  = useContext(BookContext)
    
    useEffect(() => {
        window.scrollTo(0, 0)
    },[])
    return (
    <div className="book-list__container">
        <div className="book-list__side-title book-list__side-title--left"></div>
        <div className="book-list__bookCatalog"> 
            <ul className='circles'>
                <li></li><li></li><li></li><li></li><li></li><li></li><li></li><li></li><li></li><li></li>
            </ul>
            {
                books.map((book, index) => (
                    <BookItem className="book-item__from-list"
                    key={book.id}
                    id={book.id}
                    count={index + 1}
                    title= {book.title}
                    description= {book.description}
                    img={book.img}
                    />
                ))
            }
        </div>
        <div className="book-list__side-title"></div>
    </div>
)
}

export { BookList as default }
    

